import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { MonitoringPage } from '../pages/monitoring/monitoring';
import { CysfibPage } from '../pages/cysfib/cysfib';
import { SettingsPage } from '../pages/settings/settings';
import { TabsPage } from '../pages/tabs/tabs';
import { SelfcarePage } from '../pages/self-care/self-care';
import { GuidelinesPage } from '../pages/guidelines/guidelines';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

@NgModule({
  declarations: [
    MyApp,
    MonitoringPage,
    CysfibPage,
    SettingsPage,
    TabsPage,
    SelfcarePage,
    GuidelinesPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    MonitoringPage,
    CysfibPage,
    SettingsPage,
    TabsPage,
    SelfcarePage,
    GuidelinesPage    
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



